import React from 'react'
import { Route, Routes } from 'react-router-dom'
import About from './About'
import Faq from './Faq'

const LandingPageRoutes = () => {
  return (
    <>
        <Routes>
            <Route path='/about' element={<About /> } />
            {/* <Route path='/faq' element={<Faq />} /> */}
        </Routes>
        
    
    </>
  )
}

export default LandingPageRoutes